import vueTabbarSlide from './vueTabbarSlide.vue' // 

const tabbarSlide = {
    install(Vue, options) {
        Vue.component(vueTabbarSlide.name, vueTabbarSlide) // vuePayPop.name name
        //  this.$xxx  
        // Vue.prototype.$xxx  //  this.$xxx 
        // $  
    }
}
if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.use(tabbarSlide);
}
export default tabbarSlide






